.form {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

.form label {
  display: inherit;
  flex-direction: inherit;
  margin-bottom: 1rem;
}

.form label span {
  margin-bottom: 0.25rem;
}

.form input,
.form textarea,
.form button {
  border-radius: 0.25rem;
  border: 0;
  font-family: "Poppins";
  width: 100%;
}

.form input,
.form textarea {
  padding: 0.5rem;
}

.form input::placeholder,
.form textarea::placeholder {
  opacity: 0.7;
}

.form input,
.form button {
  height: 2rem;
}

.form textarea {
  height: 10rem;
  resize: none;
}

.form button {
  background: #281f5f;
  color: white;
  cursor: pointer;
}

.back-to-list-link {
  display: flex;
  align-items: center;
}

.back-to-list-link svg {
  display: inline-block;
  margin-right: 0.5rem;
}
