.App {
  font-family: "Poppins";
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: white;
  text-decoration: none;
}

body,
html {
  font-size: 16px;
}

ul {
  list-style: none;
}

body {
  background: slateblue;
  color: white;
}

main {
  padding: 0 2rem;
  margin-top: 10rem;
  padding-bottom: 10rem;
}

@media (min-width: 760px) {
  main,
  header .inner-container {
    width: 720px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
  }
}
