.blog-posts-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.create-new-post-icon {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid white;
  border-radius: 0.25rem;
  cursor: pointer;
  background: transparent;
  color: white;
  font-size: 1.5rem;
}

.create-new-post-icon:hover {
  background: white;
  color: slateblue;
  transition: 0.1s ease-in-out all;
}

.posts-list li {
  margin-bottom: 1rem;
  background: white;
  color: black;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: 0.1s ease-in-out all;
  box-shadow: 0 0.1rem 0.1rem 0rem rgba(0, 0, 0, 0.3);
}

.posts-list li:hover {
  box-shadow: 0 0.2rem 0.5rem 0.2rem rgba(0, 0, 0, 0.2);
  transform: translateY(-0.25rem);
}

.post-header {
  display: flex;
  justify-content: space-between;
}

.post-header h2 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: 800;
  text-transform: capitalize;
}

.delete-post-icon {
  margin-left: auto;
  margin-top: auto;
  opacity: 0.5;
  transition: 0.25s ease-in-out all;
  cursor: pointer;
}

.delete-post-icon:hover {
  opacity: 1;
}

.posts-list li .post-body p {
  opacity: 0.5;
  line-height: 1.5rem;
  margin-bottom: 1rem;
}

@media (min-width: 640px) {
  .posts-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
  .posts-list li {
    width: calc(50% - 0.5rem);
  }
}
