.header {
  height: 8rem;
  padding: 0 2rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(1rem);
  margin-bottom: 2rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 10;
  color: black;
}

.header .inner-container {
  display: inherit;
  justify-content: inherit;
  flex-direction: inherit;
}

.header h1 {
  margin-bottom: 0.5rem;
}

.header h1 a {
  color: black;
  font-weight: 800;
}

.header span {
  opacity: 0.6;
}

.header span a {
  color: black;
  text-decoration: underline;
  font-weight: 900;
}
